
.theme-head {
	height: 100%;
	width: 100%;
}
.theme-head-name {
	margin-top: 0.5rem;
}
.theme-head-name p {
	text-indent: 0.5em;
}
.theme-head-name p:nth-child(2) {
	font-size: 0.5rem;
	border-left: 5px solid #efefef;
	margin: 0.5rem 2rem;
	padding: 0.2rem;
}
.theme-head-name p {
	margin: 0;
}
.theme-head-editor {
	color: grey;
}



