
.zhihu-head {
	/* ===position and size=== */
	width: 100%;
	height: 4.5rem;
	/* ===color=== */
	background-color: white;
	border-bottom: 2px solid #F5F4F4;
}
.router-link-active {
	color: black !important;
	font-weight: bold;
	font-size: 1.2rem;
	outline: none;
}
.zhihu-head a {
	color: #333333;
	outline: none;
}
@media screen and (min-width: 640px) {
.zhihu-head-icon {
		width: 25px;
		height: 25px;
}
.zhihu-head a {
		color: #333333;
		outline: none !important;
}
.zhihu-head a:hover{
		color: black;
		transition: color 1s ease;
}
}



