
.top-news {
}
.top-news-title {
  font-size: 0.8rem;
	border-left: 5px solid #efefef;
	color: grey;
	padding: 0.1rem;
	text-indent: 0.5rem;
	animation: newsLastetTime 0.5s ease 1;
	transform: translateX(0px);
	opacity: 1;
}
@keyframes newsLastetTime {
from {
		transform: translateX(100px);
		opacity: 0;
}
to {
		transform: translateX(0px);
		opacity: 1;
}
}



