
.back-to-top button {
	/* ===style format=== */
	border-top-style: none;
	border-right-style: none;
	border-bottom-style: none;
	border-left-style: none;
	outline: none;
	/* ===color=== */
	background-color: #00B4FF;
	color: white;
	font-size: 0.8rem;
	box-shadow: 0px 0px 20px #dbdada;
	/* ===size and position=== */
	position: fixed;
	right: 1rem;
	bottom: 1rem;
}



