
.description {
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
	margin: 0;
}
.github-star {
	color: black;
	padding: 5px;
}
.description p {
	text-indent: 1rem;
	line-height: 1.3rem;
	color: #4c4b4b;
}


/* ==过度动画== */
.ThemeTransition-enter-active,
.ThemeTransition-leave-active {
	transition: opacity .5s
}
.ThemeTransition-enter,
.ThemeTransition-leave-active {
	/*transform: translateX(-100vw);*/
	opacity: 0;
}



