.news-lastet-time {
	font-size: 0.8rem;
	border-left: 5px solid #efefef;
	color: grey;
	padding: 0.1rem;
	text-indent: 0.5rem;
	animation: newsLastetTime 0.5s ease 1;
	transform: translateX(0px);
	opacity: 1;
}
@keyframes newsLastetTime {
from {
		transform: translateX(100px);
		opacity: 0;
}
to {
		transform: translateX(0px);
		opacity: 1;
}
}


/* ===the style of load more button=== */
.load-more-button {
	/* ===style format=== */
	border-top-style: none;
	border-right-style: none;
	border-bottom-style: none;
	border-left-style: none;
	outline: none;
	/* ===color and font=== */
	background-color: white;
	color: black;
	font-size: 0.8rem;
	border-bottom: 1px solid #efefef;
	/* ===size and position=== */
	width: 100%;
}

/* ===back to top=== */
.back-to-top{
	/* ===style format=== */
	border-top-style: none;
	border-right-style: none;
	border-bottom-style: none;
	border-left-style: none;
	outline: none;
	/* ===color=== */
	background-color: white;
	/* ===size and position=== */
	font-size: 0.8rem;
	position: fixed;
	right: 1rem;
	bottom: 1rem;
}

/* ===the transition style of template: ZhihuHeadFix=== */
.ZhihuHeadFixClass-none {
	width: 100%;
	position: fixed;
	top: -2rem;
	transition: all 0.5s ease;
}
.ZhihuHeadFixClass-on {
	position: fixed;
	top: 0;
	width: 100%;
	transition: all 0.5s ease;
}

/* ==过度动画== */
.homeTransition-enter-active,
.homeTransition-leave-active {
	transition: opacity .5s
}
.homeTransition-enter,
.homeTransition-leave-active {
	opacity: 0;
}



